"use client";

import { AppDispatch, RootState } from "@/redux/globalStore";
import { openResponsive } from "@/redux/menuSlice";
import { faBars, faSearch } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import Image from "next/image";
import Link from "next/link";
import React from "react";
import { useDispatch, useSelector } from "react-redux";

export default function HeaderMiddle() {
  const { responsive } = useSelector((state: RootState) => state.menus);
  const dispatch = useDispatch<AppDispatch>();

  //open resposive menu
  const handleOpenResponsive = () => {
    dispatch(openResponsive(!responsive));
  };

  return (
    <div className="py-2">
      <div className="px-2 flex justify-between items-center">
        <Link href={"/"}>
          {/* logo */}
          <Image
            height={50}
            width={50}
            src={
              "https://th.bing.com/th/id/OIP.WZRv_MHPmAlGbuMLmttjmgHaHa?w=183&h=183&c=7&r=0&o=7&cb=iwp2&dpr=1.3&pid=1.7&rm=3"
            }
            alt="logo"
          />
        </Link>
        <div className="relative">
          <input
            type="text"
            className="text-center border-1 border-gray-500 w-100 outline-0 text-gray-600"
            placeholder="Search something"
          />
          <button className="absolute right-0 bg-black text-white px-2 h-full ">
            <FontAwesomeIcon icon={faSearch} />
          </button>
        </div>
        {/* responsive / sign in / logout */}
        <div className="flex gap-2">
          <button
            className="border-gray-500 border-1 text-gray-500"
            onClick={handleOpenResponsive}
          >
            <FontAwesomeIcon icon={faBars} />
          </button>
          <button className="bg-cyan-500 px-2 font-bold text-white ">
            Sign in
          </button>
        </div>
      </div>
    </div>
  );
}
